*, ::before, ::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Roboto", sans-serif;
}

.header {
  max-width: 90rem;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 6rem;
}
@media only screen and (max-width: 992px) and (orientation: landscape) {
  .header {
    height: 100vh;
    padding: 0 1.25rem;
  }
}
@media only screen and (max-width: 1200px) {
  .header {
    padding: 0;
  }
}
.header .container {
  display: flex;
  flex-direction: row;
  width: 61.25rem;
  height: 20.625rem;
  align-items: center;
  justify-content: space-between;
}
@media only screen and (max-width: 576px) {
  .header .container {
    gap: 3.75rem;
    padding: 0;
  }
}
@media only screen and (max-width: 992px) {
  .header .container {
    flex-direction: column;
    justify-content: start;
    padding: 0;
    gap: 1.875rem;
  }
}
@media only screen and (max-width: 992px) and (orientation: landscape) {
  .header .container {
    flex-direction: row;
  }
}
@media only screen and (max-width: 1200px) {
  .header .container {
    padding: 0 1.25rem;
    height: 100vh;
  }
}
.header .container .header-image {
  max-width: 100%;
  width: 31.5625rem;
  height: 20.625rem;
}
@media only screen and (max-width: 576px) {
  .header .container .header-image {
    min-width: 100%;
  }
}
@media only screen and (max-width: 992px) {
  .header .container .header-image {
    min-width: 50%;
  }
}
.header .container .content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 23.75rem;
  height: 15.625rem;
}
@media only screen and (max-width: 576px) {
  .header .container .content {
    width: 100vw;
    padding: 0 1.25rem;
  }
}
.header .container .content__title {
  font-weight: 700;
  font-size: 2.625rem;
}
@media only screen and (max-width: 768px) {
  .header .container .content__title {
    font-size: 2rem;
  }
}
.header .container .content__text {
  font-weight: 300;
  line-height: 1.5rem;
}
@media only screen and (max-width: 768px) {
  .header .container .content__text {
    font-size: 0.875rem;
  }
}
.header .container .content__btn {
  width: 8rem;
  padding: 1rem 2.5rem;
  border-radius: 2rem;
  background: #E4C1AE;
  color: #FFFFFF;
  font-weight: 700;
  border: none;
  cursor: pointer;
}
.header .container .content__btn:hover {
  background-color: #f0d0bf;
}
.header .container .content__btn:active {
  background-color: #dfb39c;
}

.alert {
  width: 100vw;
  min-height: 100vh;
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  font-family: "Roboto", sans-serif;
}
@media only screen and (max-width: 992px) {
  .alert {
    padding: 0 1.25rem;
  }
}
.alert .alert-box {
  width: 57.5rem;
  height: 13.125rem;
  background-color: #fff;
  padding: 0 2.1875rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
.alert .alert-box__title {
  font-weight: 700;
  font-size: 2.625rem;
  margin-bottom: 1.25rem;
}
.alert .alert-box__highlight {
  font-weight: 700;
}
.alert .alert-box__icon {
  width: 0.75rem;
  height: 0.75rem;
}
.alert .alert-box__btn {
  border: none;
  cursor: pointer;
  background: none;
  position: absolute;
  top: 2.875rem;
  right: 2.875rem;
}
.alert .alert-box__reset-btn-closed {
  display: none;
}
.alert .alert-box__reset-btn-opened {
  display: block;
  margin-top: 1.25rem;
  width: 8rem;
  padding: 1rem 2.5rem;
  border-radius: 2rem;
  background: #E4C1AE;
  color: #FFFFFF;
  font-weight: 700;
  border: none;
  cursor: pointer;
}
.alert .alert-box__reset-btn-opened:hover {
  background-color: #f0d0bf;
}
.alert .alert-box__reset-btn-opened:active {
  background-color: #dfb39c;
}